import styled from '@emotion/styled'
import { keyframes } from '@emotion/react'
import Box from './Box'

const anime = keyframes`
  0%,
  100% {
    transform: translate3d(0,-1.5%,0);
  }
  60% {
    transform: translate3d(0,1.5%,0);
  }
`

const Float = styled(Box)`
  animation: ${anime} 3s infinite;
`

export default Float
