import React, { useEffect, useState } from 'react'
// import FadeIn from 'react-fade-in';
import { StaticImage } from 'gatsby-plugin-image'
import loadable from '@loadable/component'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Flex from '../../../components/Flex'
import Container from '../../../components/Container'
import Source from '../../../components/Source'
import Float from '../../../components/Float'
import theme, { responsive, containerPadding } from '../../../components/ThemeProvider/theme';
import LazyLoad from '../../../components/LazyLoad'

const FadeIn = loadable(() => import('react-fade-in'))

const descriptions = [
  {
    icon: <StaticImage layout="fullWidth" src="money.png" alt="money" />,
    color: 'custom.blue',
    content: [
      <Text>排除爭議性產業及違反聯合國全球盟約(UNGC)十項原則其中一項或多項之發行人<sup>+</sup></Text>,
      <Text>剔除ESG評級最差或以碳排放量強度標準檢視為最差之發行人</Text>,
      '符合具公信力之ESG新興市場債券指數範疇*',
    ]
  },
  {
    icon: <StaticImage layout="fullWidth" src="hand.png" alt="hand" />,
    color: 'custom.darkGreen',
    content: [
      '對環境及社會帶來永續性正面影響之債券。如綠債、永續債券等',
    ]
  },
  {
    icon: <StaticImage layout="fullWidth" src="foot.png" alt="foot" />,
    color: 'custom.coffee',
    content: [
      <Text>瞄準碳排放水準較低之發行人<Box.Inline as="sup" transform="translateY(-0.25em)" fontSize="0.75rem">#</Box.Inline>，以達減碳降排之效</Text>,
    ]
  },
]

const Block = (props) => (
  <Box.Relative
    borderRadius="1em"
    bg="white"
    pl={responsive('2em', '3.75em')}
    pr={responsive('1em', '2.125em')}
    py={responsive('1.25em', '2.25em')}
    {...props}
  />
)

const Esg = ({ show }) => {
  const [firstTime, setBrowse] = useState(true)

  useEffect(() => {
    if (show) {
      setBrowse(false)
    }
  }, [show])
  return (
    <Box bg="rgba(240,244,244,0.71)" pt={theme.headerHeight} position="relative">
      <Container position="relative">
        <Text.Bold textAlign="center" color="custom.blue" fontSize={responsive('1em', '2.125em')}>
          凱基新興市場ESG永續主題債券基金
        </Text.Bold>
        <Text.Bold textAlign="center" color="custom.gray" fontSize={responsive('1em', '2.125em')}>
          (本基金有相當比重投資於非投資等級之高風險債券且配息來源可能為本金)
        </Text.Bold>
        <Flex flexDirection={responsive('column', 'row')} alignItems="center">
          <Box width={responsive('100%', '50%')}>
            <Box textAlign="center" mx={containerPadding.map(d => '-' + d)} mt={responsive('-4em', '-3em')} pointerEvents="none">
              <Float>
                <LazyLoad ratio={1}>
                  <StaticImage layout="fullWidth" src="esg_cover.png" alt="永續主題新興債"  />
                </LazyLoad>
              </Float>
            </Box>
            <Box.Relative
              mt={responsive('-5rem', '-8rem')}
              fontWeight="bold"
              textAlign="center"
              color="custom.blue"
              fontSize={responsive('1.125em', '1.75em')}
            >
              永續投資，與世界共好
            </Box.Relative>
          </Box>
          <Box
            width={responsive('100%', '50%')}
            pl={responsive(0, '4.25em')}
            pr={responsive(0, '0.75em')}
          >
            {descriptions.map(({ icon, content, color }, i) => (
              <FadeIn visible={!firstTime} delay={(i + 1) * 400} key={i}>
                <Block mt={responsive(' 2.75em' , '2.25em')} color={color}>
                  <Box.Absolute
                    width={responsive('3rem', '4.75rem')}
                    left={responsive('0.625rem', '0')}
                    top={responsive('-1.875rem', '50%')}
                    transform={responsive('none', 'translate(-50%, -50%)')}
                  >
                    <LazyLoad ratio={1}>
                      {icon}
                    </LazyLoad>
                  </Box.Absolute>
                  <ul>
                    {content.map((t, k) => (
                      <Box fontSize={responsive('0.75rem', '1.125em')} as="li" pt="0.25em" key={k}>
                        {t}
                      </Box>
                    ))}
                  </ul>
                </Block>
              </FadeIn>
            ))}
          </Box>
        </Flex>
        <Source borderColor="custom.borderBlue">
          資料來源：Insight Investment。+聯合國全球盟約 (UNGC)十項原則涵蓋人權、勞工、環保及反貪腐，違反一項或多項之發行人將與以排除。爭議性產業如透過菸草生產、賭博或武器裝備/防禦產生實質性收入之發行人。*符合JP摩根ESG新興市場企業廣泛多元債券指數或JP摩根ESG新興市場全球多元債券指數成分債券之發行人或保證人(皆含其集團母公司)。#係指低於指數平均碳排放量的發行人，指數平均為JP摩根ESG新興市場企業廣泛多元債券指數之平均。<br />資料日期：2021/6。
        </Source>
      </Container>
    </Box>
  )
}

export default Esg
